<template>
  <div>
    <Loadings v-if="data == null"></Loadings>
    <div class="container" v-else>
      <div class="item">
        <div class="info">
          <div>订单号: {{ data.data.order_no }}</div>
          <div>客户: {{ data.data.user_name }}</div>
          <div class="flex-sa">
            <div>名称: {{ data.data.name }}</div>
            <div>材质: {{ data.data.material }}</div>
          </div>
          <div class="flex-sa">
            <div>数量: {{ data.data.number }}</div>
            <div>长度: {{ data.data.length }}</div>
            <div>高度: {{ data.data.width }}</div>
          </div>
          <div>备注: {{ data.data.remark }}</div>
          <div>状态: {{ data.data.status == 0 ? "未完成" : "已完成" }}</div>
          <div>日期: {{ formatDate(data.data.add_time) }}</div>
        </div>
      </div>
      <div class="list" v-if="data.detail.length > 0">
        <div class="caption">工艺流程</div>
        <van-collapse v-model="activeNames">
          <van-collapse-item
            v-for="(item, indexs) in data.detail"
            :key="indexs"
            :title="item.pname"
            :name="indexs"
            :value="item.cstatus == 1 ? '已完成' : '待处理'"
            :disabled="item.editStatus ? false : true" 
           
          >
            <van-cell-group v-if="role != 0"> 
              <div class="flex-sa">
                <van-field
                  label="来料数  "
                  placeholder="值"
                  :disabled="item.cstatus == 1 ? true : false"
                  v-model="item.sum_num"
                />
                <van-field
                  label="完成数"
                  placeholder="值"
                  :disabled="item.cstatus == 1 ? true : false"
                  v-model="item.com_num"
                />
              </div>

              <van-field
                label="备注"
                placeholder="请输入备注内容"
                :disabled="item.cstatus == 1 ? true : false"
                v-model="item.remark"
              />

              <van-cell
                v-if="item.cstatus == 1"
                title="操作员"
                :value="item.completioner"
              />

              <van-cell
                v-else
                title="操作员"
                :value="
                  item.completioner == '' || item.completioner == 0
                    ? '点击选择操作员'
                    : item.completioner
                "
                @click="showPous(indexs)"
              />
              <van-cell
                v-if="item.completion_time"
                title="操作日期"
                :value="formatDate(item.completion_time)"
              />
              <van-cell v-if="item.cstatus == 0" center title="是否完成">
                <template #right-icon>
                  <van-switch v-model="item.checked" size="24" />
                </template>
              </van-cell>
              <div class="btn">
                <van-button
                  v-if="item.cstatus == 0"
                  @click="subPro(indexs)"
                  size="normal"
                  block
                  round
                  type="info"
                  >提交</van-button
                >
              </div>
            </van-cell-group>
          </van-collapse-item>
        </van-collapse>
      </div>
    </div>

    <van-popup v-model="showPou" position="bottom" :style="{ height: '50%' }">
      <van-picker
        @cancel="onCancel"
        @confirm="onConfirm"
        :loading="loading"
        title="操作员"
        show-toolbar
        :columns="columns"
      />
    </van-popup>
  </div>
</template>

<script>
import { setProduction, getPorderDetail, getUsers } from "@api/public";
import Empty from "@components/Empty";
import Loadings from "@components/Loadings";
import localStorage from "@utils/store/localStorage";
import {
  Icon,
  Field,
  Switch,
  Picker,
  Popup,
  Cell,
  Button,
  CellGroup,
  Collapse,
  CollapseItem,
} from "vant";
export default {
  name: "MyMessage",
  components: {
    Empty,
    Loadings,
    [Popup.name]: Popup,
    [Picker.name]: Picker,
    [Button.name]: Button,
    [Collapse.name]: Collapse,
    [CollapseItem.name]: CollapseItem,
    [CellGroup.name]: CellGroup,
    [Icon.name]: Icon,
    [Switch.name]: Switch,
    [Field.name]: Field,
    [Cell.name]: Cell,
  },
  data: function () {
    return {
      columns: [],
      data: null,
      activeNames: [],
      role: 0,
      loading: true,
      showPou: false,
      nowInd: 0,
    };
  },
  created() {
    var jsonData = JSON.parse(localStorage.get("userInfo"));
    this.role = jsonData["role"];
    this.getData();
    //////
    getUsers({ type: 1 })
      .then(({ data }) => {
        var arr = [];
        data.forEach((item) => {
          arr.push(`${item.signature} ${item.user_nickname}`);
        });
        this.loading = false;
        this.columns = arr;
      })
      .catch((e) => {
        this.$dialog.message(e.msg);
      });
  },
  mounted: function () {},
  methods: {
    clickTips(e){
      console.log(e);
      if(e){
          this.$dialog.message('请先完善上一步工艺');
      }
    },
    getData() {
      getPorderDetail({ id: this.$route.params.type }).then((res) => {
        var arr = [];
        var data = res.data.detail;
        data.forEach((item) => {
          item.editStatus = false;
        });
        for (var i = 0; i < data.length; i++) {
          data[i].checked = data[i].cstatus;
          if (i == 0) {
            data[0].editStatus = true;
          }
          if (data[i].cstatus == 1) {
            data[i].editStatus = true;
            data[i + 1].editStatus = true;
          }
          console.log(i);
        }

        this.data = res.data;
        this.activeNames = arr;
      });
    },
    subPro: function (index) {
      var data = this.data.detail[index];
      setProduction({
        pcid: data.pcid,
        poid: this.data.data.oid,
        com: data.com_num,
        sum: data.sum_num,
        cstatus: data.checked && data.checked != 0 ? 1 : 0,
        remark: data.remark,
        completioner: data.completioner,
      }).then((res) => {
        this.$dialog.message("修改成功");
        this.getData();
      });
    },
    onCancel() {
      this.showPou = false;
    },
    onConfirm(value) {
      var data = this.data;
      data["detail"][this.nowInd]["completioner"] = value;
      this.data = data;
      this.showPou = false;
    },
    showPous(ind) {
      this.showPou = true;
      this.nowInd = ind;
    },
    formatDate: function (value) {
      let date = new Date(value * 1000);

      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? "0" + MM : MM;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;

      return y + "-" + MM + "-" + d + " ";
    },
  },
};
</script>
<style scoped>
.btn {
  margin: 20px 0;
}
.container {
  box-shadow: 0 0 5px #eee;
  background: #fff;
  border-radius: 5px;
}
.caption {
  line-height: 50px;
  text-align: center;
}
.item {
  text-align: center;
  font-size: 14px;
  padding: 10px;
  text-align: left;
  color: #666;
  line-height: 22px;
  border-bottom: 1px solid #eee;
}
.item .info div {
  width: 100%;
}
</style>
